import ipc from "@achrinza/node-ipc";
import { IPC_EVENTS, IPC_EXTENSION_ID } from "@root/core/const/ipc.const";

import { wrapCommand } from "@/cli/commands/common";
import { Program } from "@/cli/program.types";

const COMMAND_NAME = "login";

export const bootstrapLogin = (program: Program) =>
  program
    .command("login")
    .description("login to vscode extension using JWT")
    .argument("jwt", "JWT token")
    .action(async (jwt) => {
      const launchCommand = () => {
        ipc.of[IPC_EXTENSION_ID].emit(IPC_EVENTS.LOGIN_REQUEST, jwt);
      };

      await wrapCommand(program, COMMAND_NAME, launchCommand);
    });
