import * as process from "node:process";

import { Command } from "@commander-js/extra-typings";

import { bootstrapGenerateTestSummary } from "@/cli/commands/generate-test-summary";
import { bootstrapPing } from "@/cli/commands/ping";
import { Program } from "@/cli/program.types";

import { version } from "../../package.json";
import { CLI_DESCRIPTION, CLI_NAME } from "./cli.const";
import { bootstrapCommand } from "./commands/command";
import { bootstrapLogin } from "./commands/login";

export const createCommand = async () => {
  const program: Program = new Command();

  program
    .name(CLI_NAME)
    .description(CLI_DESCRIPTION)
    .version(version, "-v, --version")
    .option("--verbose", "enable verbose output");

  bootstrapCommand(program);
  bootstrapLogin(program);
  bootstrapGenerateTestSummary(program);
  bootstrapPing(program);

  program.parse(process.argv);
};
