import process from "node:process";
import { setTimeout as wait } from "node:timers/promises";

import { IPC_COMMAND_RESPONSE_MESSAGE } from "@root/core/const/ipc.const";
import { isDefined } from "@root/core/utils/common.utils";
import { Fn } from "@root/types/common.types";
import { IPCCommandResponse } from "@root/types/ipc.types";

import { WrapCommandOptions, WrapCommandOptionsResponseHandler } from "@/cli/commands/common.types";
import { initIPCConnection } from "@/cli/ipc";
import { Program } from "@/cli/program.types";
import { mutex, observer, timeoutController } from "@/cli/sync";

const defaultResponseHandler: WrapCommandOptionsResponseHandler = ({
  commandName,
  response: { code, isSuccess, message },
}) => {
  if (isDefined(message)) {
    console.info(message);
  }

  const codeText = IPC_COMMAND_RESPONSE_MESSAGE[code].replaceAll("{{command}}", commandName);

  if (isSuccess) {
    console.info(codeText);
  } else {
    console.error(codeText);
  }

  process.exit(isSuccess ? 0 : 1);
};

const DEFAULT_OPTIONS: WrapCommandOptions = {
  responseHandler: defaultResponseHandler,
};

const MAX_DELAY = 1000;

export const wrapCommand = async (
  program: Program,
  commandName: string,
  launchCommand: Fn<void>,
  _options?: Partial<WrapCommandOptions>,
) => {
  try {
    await initIPCConnection(isDefined(program.opts().verbose));

    const { responseHandler } = {
      ...DEFAULT_OPTIONS,
      ..._options,
    };

    observer.addListener((response: IPCCommandResponse) => responseHandler({ commandName, response }));

    const timeout = async () => {
      await wait(MAX_DELAY, null, { signal: timeoutController.signal });

      console.info(`Can't connect to the extension`);
      process.exit(1);
    };

    const run = async () =>
      await mutex.runExclusive(async () => {
        launchCommand();
        console.info(`"${commandName}" executed in vscode extension`);
      });

    await Promise.race([timeout(), run()]);
  } catch {
    console.error(`Failed running command "${commandName}"`);
    process.exit(1);
  }
};
