import ipc from "@achrinza/node-ipc";
import { Argument } from "@commander-js/extra-typings";
import {
  AVAILABLE_COMMANDS,
  IPC_COMMAND_RESPONSE_MESSAGE,
  IPC_EVENTS,
  IPC_EXTENSION_ID,
} from "@root/core/const/ipc.const";
import { COMMANDS_PREFIX } from "@root/core/const/vscode.const";

import { wrapCommand } from "@/cli/commands/common";
import { WrapCommandOptionsResponseHandler } from "@/cli/commands/common.types";
import { Program } from "@/cli/program.types";

const COMMAND_NAME = "command";

export const bootstrapCommand = (program: Program) =>
  program
    .command(COMMAND_NAME)
    .description("execute vscode command")
    .alias("c")
    .addArgument(
      new Argument("command", "vscode command to execute").choices(
        AVAILABLE_COMMANDS.map((command) => command.replace(COMMANDS_PREFIX + ".", "")),
      ),
    )
    .action(async (command) => {
      const launchCommand = () => {
        ipc.of[IPC_EXTENSION_ID].emit(IPC_EVENTS.COMMAND_REQUEST, COMMANDS_PREFIX + "." + command);
      };

      const responseHandler: WrapCommandOptionsResponseHandler = ({ response: { code, isSuccess } }) => {
        const message = IPC_COMMAND_RESPONSE_MESSAGE[code].replaceAll("{{command}}", command);

        if (isSuccess) {
          console.info(message);
        } else {
          console.error(message);
        }

        process.exit(isSuccess ? 0 : 1);
      };

      await wrapCommand(program, COMMAND_NAME, launchCommand, {
        responseHandler,
      });
    });
