import ipc from "@achrinza/node-ipc";
import { Argument, Option } from "@commander-js/extra-typings";
import { IPC_EVENTS, IPC_EXTENSION_ID } from "@root/core/const/ipc.const";
import { GenerateTestSummaryRequest } from "@root/types/ipc.types";

import { wrapCommand } from "@/cli/commands/common";
import { Program } from "@/cli/program.types";

const COMMAND_NAME = "generate-test-summary";

export const bootstrapGenerateTestSummary = (program: Program) =>
  program
    .command(COMMAND_NAME)
    .description("generate test summary in json format using specific LLM model")
    .alias("gts")
    .addArgument(new Argument("model", "model name to test on"))
    .addOption(new Option("--database-url [databaseUrl]", "postgres remote database to push data"))
    .action(async (model, { databaseUrl }) => {
      const launchCommand = () => {
        ipc.of[IPC_EXTENSION_ID].emit(IPC_EVENTS.GENERATE_TEST_SUMMARY_REQUEST, {
          model,
          databaseURL: databaseUrl?.toString(),
        } satisfies GenerateTestSummaryRequest);
      };

      await wrapCommand(program, COMMAND_NAME, launchCommand);
    });
