import { Fn } from "@/types/common.types";

export class Observer<T> {
  private readonly listeners: Array<Fn<T>> = [];

  // it would be nice to have some disposable mechanism
  public addListener(listener: Fn<T>): void {
    this.listeners.push(listener);
  }

  public notifyAll(data: T) {
    for (const listener of this.listeners) {
      listener(data);
    }
  }
}
