import ipc from "@achrinza/node-ipc";
import { IPC_EVENTS, IPC_EXTENSION_ID } from "@root/core/const/ipc.const";
import { IPCCommandResponse } from "@root/types/ipc.types";

import { mutex, observer, timeoutController } from "@/cli/sync";

ipc.config.id = "early-cli";
ipc.config.retry = 1500;
ipc.config.silent = true;

const RESPONSE_EVENTS = [
  IPC_EVENTS.COMMAND_RESPONSE,
  IPC_EVENTS.LOGIN_RESPONSE,
  IPC_EVENTS.GENERATE_TEST_SUMMARY_RESPONSE,
  IPC_EVENTS.PING_RESPONSE,
];

export const initIPCConnection = async (isVerbose: boolean) => {
  await mutex.acquire();

  if (isVerbose) {
    ipc.config.silent = false;
  }

  ipc.connectTo(IPC_EXTENSION_ID, () => {
    ipc.of[IPC_EXTENSION_ID].on("connect", () => mutex.release());
    ipc.of[IPC_EXTENSION_ID].on(IPC_EVENTS.EVENT_RECEIVED, () => timeoutController.abort());

    for (const responseEvent of RESPONSE_EVENTS) {
      ipc.of[IPC_EXTENSION_ID].on(responseEvent, (response: IPCCommandResponse) => observer.notifyAll(response));
    }
  });
};
