export const isDefined = <T>(v: T | null | undefined): v is T => v !== undefined && v !== null;
export const isObject = (v: unknown): v is Record<string, unknown> => typeof v === "object" && v !== null;
export const isArray = (v: unknown): v is Array<unknown> => Array.isArray(v);
export const isMap = (v: unknown): v is Map<unknown, unknown> => v instanceof Map;
export const isSet = (v: unknown): v is Set<unknown> => v instanceof Set;
export const isDate = (v: unknown): v is Date => v instanceof Date;
export const isNumber = (v: unknown): v is number => typeof v === "number";
export const isString = (v: unknown): v is string => typeof v === "string";
export const isBoolean = (v: unknown): v is boolean => typeof v === "boolean";
export const isError = (v: unknown): v is Error => v instanceof Error;

/**
 * @deprecated Use `isEmpty` instead
 */
export const isEmptyString = (v: string): boolean => v.length === 0;
/**
 * @deprecated Use `isEmpty` instead
 */
export const isEmptyObject = (v: object): boolean => Object.keys(v).length === 0;

export const isEmpty = (v: unknown): boolean => {
  if (!isDefined(v)) {
    return true;
  }

  if (isString(v) || isArray(v)) {
    return v.length === 0;
  }

  if (isMap(v) || isSet(v)) {
    return v.size === 0;
  }

  if (isDate(v)) {
    return false;
  }

  if (isObject(v)) {
    return Object.keys(v).length === 0;
  }

  return false;
};

export const wait = (ms: number) => new Promise((resolve) => setTimeout(resolve, ms));
export const getErrorMessage = (error: unknown): string =>
  isError(error) ? error.message : "error is not Instance of Error";
